// === THEME ===
// Allowed values: Dark, Light, Default
$theme: Light;

// === COLORS ===
$text-color: #fff;
$body-background-color: #22222a;
$theme-color: #42424a;
$secondary-color: #c24721;
$ternary-color: #698932;
$link-color: $secondary-color;
$menu-item-color: white; 

@if $theme == Dark {
    $text-color: #fff;
    $body-background-color: #22222a;
    $theme-color: #42424a;
    $secondary-color: #c24721;
    $ternary-color: #698932;
    $link-color: $secondary-color;
    $menu-item-color: white; 
} @else if $theme == Light {
    $text-color: #000;
    $body-background-color: #fff;
    $theme-color: #372a27;
    $secondary-color: #d26741;
    $ternary-color: #b9da63;
    $link-color: $secondary-color;
    $menu-item-color: white; 
}

// === FONTS ===
$text-font: "Alegreya Sans", Ariel, sans-serif;
$headline-font: "Titillum Web", Georgia, serif;

// === SIZES ===
$content-widht: 960px;
$header-height: 60px;
$footer-height: 90px;
$col: col-width($columns: 8);